import { useState } from 'react';
import { useSelector } from '../../services/hooks';
import { useHistory, useLocation } from 'react-router-dom';

import { Button, EmailInput, PasswordInput } from '@ya.praktikum/react-developer-burger-ui-components';
import FormWrapper from '../form-wrapper';
import SpanWithLink from '../span-with-link';
import { logIn } from '../../services/actions/user';

export const LoginForm = () => {
  const { logInFailed, logInFailedMessage } = useSelector((state) => ({
    logInFailed: state.user.logInFailed,
    logInFailedMessage: state.user.logInFailedMessage
  }))
  interface LocationState {
    from: {
      pathname: string;
    };
  }
  const history = useHistory();
  const location = useLocation<LocationState>();

  const [state, setState] = useState({
    email: '',
    password: ''
  });

  const handleInputChange: React.ChangeEventHandler = (e: React.ChangeEvent<HTMLInputElement>) => {
    const target = e.target;
    setState({
      ...state,
      [target.name]: target.value
    });
  }

  const { from } = (location.state) || { from: { pathname: "/" } };
  const cb = () => {
    history.replace(from);
  };

  return (
    <FormWrapper
      title=""
      actionFunc={logIn(state.email, state.password, cb)}
      validateFunc={undefined}
    >
      <div className="mb-6">
        <EmailInput
          onChange={handleInputChange}
          value={state.email}
          name={'email'}
        />
      </div>
      <div className="mb-6">
        <PasswordInput
          onChange={handleInputChange}
          value={state.password}
          name={'password'}
        />
      </div>
      {logInFailed && logInFailedMessage !== '' ? 
        <p className="text text_type_main-medium text_color_inactive mb-5">
          {logInFailedMessage.charAt(0).toUpperCase() + logInFailedMessage.slice(1)}
        </p>
        :
        <></>
      }
      <div className="mb-20">
        <Button type="primary" size="medium">
          
        </Button>
      </div>
      <SpanWithLink
        buttonText=""
        link="/register"
        spanText="   ?"
        mb="4"
      />
      <SpanWithLink
        buttonText=" "
        link="/forgot-password"
        spanText=" ?"
        mb="0"
      />
    </FormWrapper>
  )
}