import { useEffect, useMemo } from 'react';
import { useDispatch, useSelector } from '../../services/hooks';

import styles from './order-details.module.css';
import { CheckMarkIcon } from '@ya.praktikum/react-developer-burger-ui-components';
import Preloader from '../preloader';
import { ORDER_RESET } from '../../services/actions/order';
import { TOrderFull } from '../../services/types/data';

const OrderDetails = () => {
  const { order, orderRequest, orderFailed } = useSelector(state => ({
    order: state.order.order,
    orderRequest: state.order.orderRequest,
    orderFailed: state.order.orderFailed,
  }));
  const dispatch = useDispatch();

  useEffect(
    () => {
    return () => {
      dispatch({ type: ORDER_RESET })
    }
  },[]
  )

  const content = useMemo(
    () => {
      return orderRequest ? (
        <Preloader />
        ) : (
        orderFailed ? (
          <p className="text text_type_main-medium"> .   .</p>
        ) : (
          <>
            <p className="text text_type_digits-large mb-8">{(order as TOrderFull).number}</p>
            <p className="text text_type_main-medium mb-15"> </p>
            <div className={`mb-15 ${styles.icon_wrapper}`}>
              <CheckMarkIcon type="primary"/>
            </div>
            <p className="text text_type_main-default mb-2">   </p>
            <p className="text text_type_main-default text_color_inactive">    </p>
          </>
        ))
    },
    [order, orderFailed, orderRequest]
  );

  return (
    <>
      {content}
    </>
  );
}

export default OrderDetails